
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import { getBpDatas, updateBpPrice, updateBpSelect } from '@/api/'
export default {
props: ['list'],
data () {
return {
themeSelected: [],
themes: [],
editIndex: -1
}
},
created () {
getBpDatas({ht_id: this.$route.params.id, type: 2}).then((res) => {
let selecteds = []
res.result.forEach((v, i) => {
if (Number(v.selected) === 1) {
selecteds.push(i)
}
})
this.themeSelected = selecteds
this.themes = res.result
})
},
mounted () {

},
methods: {
bpfilterList (screens) {
return screens.filter((v) => {
return v.title !== '重金霸屏'
})
},
onClick (itemValue, itemDisabled) {
this.themes[itemValue].selected = Number(this.themes[itemValue].selected) === 1 ? 0 : 1
let data = {
id: this.themes[itemValue].id,
selected: this.themes[itemValue].selected,
ht_id: this.$route.params.id
}
updateBpSelect(data)
console.log(data)
},
edit (index) {
let _this = this
this.editIndex = index
this.$vux.confirm.prompt('价格', {
title: '请输入价格',
onShow () {
console.log('promt show')
_this.$vux.confirm.setInputValue(_this.themes[index].price)
},
onHide () {
console.log('prompt hide')
},
onCancel () {
console.log('prompt cancel')
},
onConfirm (msg) {
let data = {
price: msg,
id: _this.themes[_this.editIndex].id,
ht_id: _this.$route.params.id

}
updateBpPrice(data).then((res) => {
_this.themes[_this.editIndex].price = Number(msg).toFixed(2)
})
}
})
}
},
components: {
Checker,
CheckerItem,
CheckIcon
}
}



